export default {
	data: function() {
		return {
			data: [],
			time: '2017-06-27 00:00:00'
		}

	},
	methods: {

	
		gettime: function() {

			this.time = new Date().getTime()

		},
		minutime: function() {
			return this.time = this.time - (1000 * 60 * 60 * 24)

		},
		addtime: function() {
			return this.time = this.time + (1000 * 60 * 60 * 24)

		}

	},
	mounted() {
		this.gettime()

	}
}



